import React, { Component } from 'react';
import '../css/NavBar.css';
import MemerSVG from '../css/memer.svg';

class NavBar extends Component
{
  render()
  {
    let topNav = window.matchMedia("(min-width: 400px)").matches ? "Big Boi" : 'Small boi';
    
    return(
      <nav>
        <div className="inner-nav">
          <a>Info</a>
          <a>Commands</a>
          <a>Team</a>
        </div>
        <MemerSVG className="memer noselect"/>
        <span className="title noselect">{topNav}</span>
        <span className="meme-line noselect">Meme me, daddy.</span>
      </nav>
    )
  }
}

export default NavBar;



